/* Styles for layout/checkout.liquid */

:root {
  --container-inline-size: 70.5rem;
  --container-padding-inline: clamp(20px, calc(20px + 20 * (100vw - 500px) / 254), 40px);
  --container-padding-block-start: clamp(45px, calc(45px + 40 * (100vw - 500px) / 254), 85px);
  --container-padding-block-end: clamp(45px, calc(45px + 40 * (100vw - 500px) / 254), 85px);
  --color: hsla(0, 19%, 13%, 1);
  --bg-color: hsla(0, 0%, 100%, 1);
  --accent-color: hsla(0, 100%, 46%, 1);
  --border-color: hsla(359, 5%, 70%, 1);
  --border: 1px solid var(--border-color);
  --transition-duration: 150ms;
  --transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Import only checkout styles, not other components */

.product-thumbnail {
  block-size: 6.6em;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.product-thumbnail__wrapper {
    border-radius: 0;
  }

.product-thumbnail__quantity {
    background-color: var(--accent-color);
    border-radius: 0;
  }

.product-thumbnail::after {
    border: 0 rgba(0, 0, 0, 0) solid;
    border-radius: 0;
  }

.breadcrumb__link:hover {
  text-decoration-line: underline;
}

.step__footer__continue-btn {
  background-color: var(--accent-color);
  border-radius: 0;
  transition: border-radius var(--transition-duration)
    var(--transition-timing-function);
}

.step__footer__continue-btn:hover {
    border-radius: 20px;
    background-color: var(--accent-color);
  }

